html {
  box-sizing: border-box;
}
/* dealing with padding expanding width
https://www.paulirish.com/2012/box-sizing-border-box-ftw/ */
*,
*:before,
*:after {
  box-sizing: inherit;
}

body{
  background-color: #282c34;
}

#root {
  /* background-color: #282c34; */
  text-align: center;
  text-align: -webkit-center;
}

.App {
  max-width: 800px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #282c34;
  color: white;
}

#header {
  text-align: center;
  margin: 15px;
  width: 95%;
}

#share {
  float: left;
}

#gitHub {
  float: right;
}

#header h1,
#header h3 {
  display: inline;
}

#display {
  margin: 2px;
  margin-bottom: 20px;
  width: 95%;
  padding: 5px;
  background: whitesmoke;
  color: black;
  border-radius: 5px;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

li {
  text-align: initial;
  position: relative;
  margin: 4px;
  padding: 3px;
  background: silver;
  border-radius: 5px;
}

li h3,
li p {
  margin: 0;
  padding: 2px;
  word-break: break-all;
}

li button {
  margin: 0;
  float: right;
  position: absolute;
  top: 3px;
  right: 3px;
}

button {
  background: white;
  border-radius: 5px;
  padding: 5px 7px;
  font-size: max(1em, 12px); /* use largest from list, thus setting the minimum size */
}

#form {
  width: 95%;
  text-align: center;
}

input {
  margin-bottom: 5px;
  width: 100%;
  padding: 10px;
  font-size: max(1em, 12px);
  border-radius: 5px;
}

h4 {
  text-align: center;
  margin: 3px;
}

#create {
  float: left;
}

#install {
  float: right;
}

#share:hover {
  cursor: pointer;
}
